export default {
	data() {
		return {
			accountForm: {
				accountname: '',
				account: '',
				password: '',
				theserver: '',
				owner: '',
				remark: '',
				isOnline: 0
			},
			accountFormArr:[
				['accountname', '游戏昵称'],
				['account', '账号'],
				['password', '密码'],
				['theserver', '服务器'],
				['owner', '号主'],
				['remark', '备注']
			]
		}
	},
	onLoad (option) {
		this.axios.get('/v1/findAccount',{params:option}).then( res => {
			if(res.data.length == 1){
				this.accountForm = res.data[0]
			}else{
				console.log("出错了")
			}
		})
	},
	methods: {
		formSubmit () {
			var data = this.accountForm
			let loginRules = [
			  {name: 'account',type: 'required', errmsg: '账号为必填项'},
			  {name: 'password', type: 'required', errmsg: '密码为必填项'},
			]
			let valLoginRes = this.$validate.validate(data, loginRules)
			if (!valLoginRes.isOk) {
			  uni.showToast({
			    icon: 'none',
			    title: valLoginRes.errmsg
			  })
			  return false
			}
			this.axios.post('/v1/upAccount',data).then(res => {
				if(res.data == "修改成功"){
					uni.showToast({
					  icon: 'none',
					  title: res.data
					})
					setTimeout(() => {
						uni.hideToast()
						uni.switchTab({
						    url: '/pages/account/index'
						})
					},1000)
				}
			})
		},
		backList () {
			uni.navigateBack({
			    delta: 1
			});
		},
		changeOnlie (e) {
			if (this.accountForm.isOnline==0){
				this.accountForm.isOnline = 1
			}else{
				this.accountForm.isOnline = 0
			}
		}
	}
}
